body{
  font-family: sans-serif;
  font-size: 16px;
}

span{
  font-size: 1.2em;
  font-style: italic;
  font-weight: bolder;
  
}
#main{
  background: mediumaquamarine;
  /* background: mediumaquamarine; */
}

#search{
  color: blue;
  text-align: center;
}
#bgColor{
  margin-left: 20px;
}
button{
  border-radius: 5px;
}

#search input{
  width: 40%;
  border-radius: 10px;
  border-top: none;
  box-shadow: 2px 2px 3px lightslategray;
  height: 200%;
}
#imggithub{
  width: 100px;
  height: 100px;
  border-radius: 30%;
}

#result{
  text-align: center;
  margin-top: 20px;
}
#result{
  display: flex;
  justify-content: space-around;
 
}
.card1{
  flex-basis: 300px;
  flex-shrink: 2;
  flex-wrap: wrap;
}
.item{
  outline: 1px solid gray;
  flex-shrink: 2;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 3px;
  box-shadow: 2px 2px 3px gray;
}
.card1 :hover{
  background-color: lightsteelblue;
}

#circle{
  background: cyan;
  padding: 10px;
  border-radius: 30px;
}
.item i{
  color: chocolate;
}
footer{
  background: black;
  color: lightgray;
}
footer ul{
 text-align: center;
 padding: 20px; 
}

ul li{
  display: inline;
  margin: 20px;
}

